import VehicleTypeadd from '../components/rental/VehicleTypeadd'
import VehicleTypevw from '../components/rental/VehicleTypevw'
import RentalRateadd from '../components/rental/RentalRateadd'
import RentalRatevw from '../components/rental/RentalRatevw'

const RentalRoutes = [
  {
    path: '/rental/RentalRate/add',
    name: 'RentalRateadd',
    component: RentalRateadd
  },
  {
    path: '/rental/RentalRate',
    name: 'RentalRatevw',
    component: RentalRatevw
  },
  {
    path: '/rental/VehicleType/add',
    name: 'VehicleTypeadd',
    component: VehicleTypeadd
  },
  {
    path: '/rental/VehicleType',
    name: 'VehicleTypevw',
    component: VehicleTypevw
  }
]

export default RentalRoutes



